@import '../abstract/variables';

$border-button-none: $no-border;
$border-radius-button-base: $border-radius-base;

$space-padding-button-y: $space-condensed;
$space-padding-button-x: $space-default;

$color-text-button: $white;
$color-text-button-secondary: $black-900;

$font-size-button-base: $font-base;
$font-weight-button-bolder: $text-bolder;

$line-height-tabs-small: $lineheight-small;

$icon-padding-muibutton-y: $space-nospace;
$icon-padding-muibutton-x: $space-micro;



$theme-colors: (
    "primary":$primary-600,
    "success":$success-700,
    "danger":$danger-800,
    "warning":$warning-400,
    "secondary":$black-200,

    ) !default;



// Button Mixings
@mixin btn($bg-color) {
    text-decoration: none;
    cursor: pointer;
    display: inline-block;
    border: $border-button-none;
    padding: $space-padding-button-y $space-padding-button-x;
    border-radius: $border-radius-button-base;
    background-color: $bg-color;
    color: $color-text-button;
    font-size: $font-size-button-base;
    font-weight: $font-weight-button-bolder;
    line-height: $line-height-tabs-small;

    em {
        padding: $icon-padding-muibutton-y $icon-padding-muibutton-x;
    }

    +.btn {
        margin-left: 14px;
    }

}



@each $key, $val in $theme-colors {
    .btn-#{$key} {
        @include btn($val
    );

    &:hover {
        background-color: darken($val, 10%);
    }

    &[disabled] {
        pointer-events: none;
        opacity: 0.35;
    }

    &:active {
        background-color: darken($val, 20%);
    }

    &:focus {
        border: 1px solid darken($val, 20%);
    }
}

@if $key =='secondary' {
    .btn-#{$key} {
        color: $color-text-button-secondary;
    }
}

.btn-#{$key}-link {
    // @include btn($transparent);
    color: $val;
    text-decoration: none;
    padding: $space-padding-button-y $space-padding-button-x;
    font-size: $font-size-button-base;
    font-weight: $font-weight-button-bolder;

    &:hover {
        color: darken($val, 20%);
        text-decoration: underline;
        cursor: pointer;

    }

    &[disabled] {
        pointer-events: none;
        opacity: 0.35;
    }

    &:active {
        color: darken($val, 40%);
    }

    &:focus {
        background-color: ($val, 60%);
        border: 1px solid ($val, 80%);
    }
}

}

